/* General Styles 
-------------------------*/
body {
  direction: rtl;
  unicode-bidi: embed;
}

table {
  margin-bottom: 30px;
}

td, th {
  padding: 0 0 0 1em;
}

dd {
  margin-left: 0;
  margin-right: 1em;
}

ul, ol {
  padding-left: 0;
  padding-right: 20px;
}

input[type=checkbox], input[type=radio] {
  margin-right: 0;
  margin-left: 0.5em;
}

input[type=file] {
  margin-right: 0;
  margin-left: 5px;
}

.tooltip.right {
  margin-left: 0;
  margin-left: 2px;
}

.tooltip.left {
  margin-left: 0;
  margin-right: -2px;
}

.panel-group .panel .panel-heading {
  overflow: hidden;
}

.panel-group .panel .panel-heading .panel-title {
  float: right;
}

.panel-group .panel .panel-heading .details {
  float: left;
}

.panel-group .panel .panel-heading .details span {
  margin-left: 0;
  margin-right: 1em;
}

.social i, .social-transparent i {
  margin: 0 0 10px 5px;
}

.section-title::after {
  left: 100%;
  right: 0;
  margin-left: 0;
  margin-right: 1em;
}

.section-title span {
  padding-right: 0;
  padding-left: 1em;
}

.section-title span::after {
  left: 100%;
  right: 0;
}

.content-with-details > div .on-hover {
  left: 100%;
  right: 0;
}

